@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
* {
  box-sizing: border-box;
}
html {
  font-size: 14px;
}


h1 {
  text-align: center;
  font-size: 2rem;
  margin: 0 auto;
  background: #30475e;
  color: #fff;
  padding: 10px 0px;
}
h2{
  margin:0;
  padding:0;
  font-weight:200;
  font-size:0.7rem;
}
body {
  margin: 0;
  font-family: Roboto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: Roboto;
}

#root {
  height: 92vh;
}
.container {
  margin: 0 auto;
  max-width: 800px;
  border: 2px solid #30475e;
  position: relative;
  height: 100%;
}

.blog-input-container {
  border-top: 0.5px solid #00c397;
}

.blogs {
  overflow: auto;
  height: calc(100% - 116px);
}

.blog-article {
  margin-bottom: 2rem;
}
.blog-item {
  display: grid;
  grid-template-columns: 10% 1fr 10%;
  grid-template-rows: auto;
  padding: 0.63rem 0 3.75px 0;
  grid-gap: 0.5rem;
  /* margin-bottom: 2rem; */
}
.reply {
  padding: 0 0 0 52px;
}
.blog-item-header-flex {
  display: flex;
  justify-content: space-between;
}
.blog-item-header-name,
.blog-item-header-timestamp {
  font-size: 0.8rem;
}
.blog-item-container {
  position: relative;
}
.blog-item-content {
  margin: 0;
  grid-column-start: 2;
  grid-column-end: 4;
  background: #f6f6f6;
  border-radius: 8px;
  padding: 20px;
  font-size: 1.2rem;
}

.mycontent {
  background: #00c397;
  color: white;
}
.blog-item-avatar {
  width: 32px;
  height: 32px;
  line-height: 32px;
  border-radius: 50%;
  font-size: 16px;
  color: #fff;
  text-align: center;
  background: #30475e;
  margin-left: 0.4rem;
}
.right {
  margin: 0;
}
@media (min-width: 768px) {
  html {
    font-size: 16px;
  }
  .blog-item-avatar {
    width: 68px;
    height: 68px;
    line-height: 68px;
    font-size: 32px;
  }
}
@media (min-width: 1024px) {
  html {
    font-size: 18px;
  }
}
@media (min-width: 2048px) {
  html {
    font-size: 20px;
  }
}

.feature {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #c1a57b;
  align-items: start;
  padding-top: 0.2rem;
  -webkit-animation: fade 0.5s;
          animation: fade 0.5s;
}
.feature-item {
  display: flex;
  justify-content: center;
  align-items: center;
  /*padding-top: 1rem;*/
}
.feature-item-button {
  margin: 0 4px 0 0;
  padding: 0;
  background: none;
  border: none;
  fill: #fff;
  outline: none;
  cursor: pointer;
}
.selected {
  fill: rgb(167, 20, 20);
}
.feature-item-counter {
  font-size: 0.88rem;
  color: white;
  padding-right: 1rem;
}

.reply-input-container {
  border-top: 0.5px solid #30475e;
}

@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.input-container {
  display: flex;
  width: 100%;
  /*position: absolute;
   bottom: 0; */
  align-items: center;
  height: 50px;
}
.input {
  width: 100%;
  outline: 0;
  border: 0;
  font-size: 1.2rem;
  font-weight: 600;
}
.input-counter {
  padding: 0.8rem;
  font-weight: 600;
  font-size: 0.7rem;
  letter-spacing: 1px;
  opacity: 0.4;
}
.input-button {
  margin: 0 1rem 0 1rem;
  padding: 0;
  background: none;
  border: none;
  fill: #30475e;
  outline: none;
  cursor: pointer;
}

.input-button:active {
  position: relative;
  top: 1px;
}
.input-button:disabled {
  fill: #b7b7b7;
  cursor: default;
  position: unset;
}

.input-button-svg {
  height: 2rem;
  width: 2rem;
}

